body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/*Обнуление*/
* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}

/*Конец обнуления*/

$orange: #FE5F1E;
$orangeHover: #ff7b47;
$orangeActive: #fd9871;
$blackLight: #2C2C2C;
$grayBG: #F9F9F9;

@font-face {
  font-family: 'Proxima Nova';
  src: url("./fonts/proximanova_regular.ttf");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Proxima Nova';
  src: url("./fonts/proximanova_bold.otf");
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: 'Proxima Nova';
  src: url("./fonts/proximanova_extrabold.otf");
  font-weight: 800;
  font-style: normal;
}

body {
  background-color: #FFDF8C;
}

#root {
  padding: 45px;
  background-color: #FFDF8C;
  font-family: 'Proxima Nova';
  color: #000000;
}

h1 {
  font-family: 'Proxima Nova';
  font-size: 32px;
  font-weight: 700;
  line-height: 39px;
  letter-spacing: 0.01em;
}

.app {
  max-width: 1340px;
  margin: 0 auto;
  border-radius: 10px;
  background-color: #ffffff;
  box-shadow: 0 0 20px 0 #0000001c;
}

.main {
  position: relative;
}

@media (max-width: 600px) {
  #root {
    padding: 15px;
  }
}

@media (max-width: 370px) {
  h1 {
    font-size: 22px;
  }
}