@import './../../../index.scss';

.sortByOther {
    position: relative;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    min-width: 225px;
    margin-left: 25px;
    -moz-user-select: none;
    -khtml-user-select: none;
    user-select: none;

    img {
        width: 10px;
        height: auto;
        margin-right: 7px;
        cursor: pointer;
    }

    span {
        font-family: 'Proxima Nova';
        font-style: normal;
        font-weight: 700;
        font-size: 14px;
        line-height: 17px;
        letter-spacing: 0.015em;
        color: $blackLight;
    }

    .sortName {
        margin-left: 5px;
        border-bottom: 1px dashed $orange;
        font-weight: 400;
        color: $orange;
        cursor: pointer;

        &:hover {
            border-bottom: none;
            color: #ffa987;
            transition: 0.3s;
        }

        &:active {
            color: #651d00;
        }
    }

    .sortSelector {
        display: none;
        position: absolute;
        top: 26px;
        right: 0;
        width: 132px;
        height: 136px;
        padding: 13px 0;
        border-radius: 10px;
        background-color: #ffffff;
        box-shadow: 0 0 20px 0 #0000001c;
        z-index: 2;
        -webkit-touch-callout: none;
        -webkit-user-select: none;
        -khtml-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;

        ul {
            display: grid;
            grid-template-columns: 1fr;
            grid-template-rows: 1fr 1fr 1fr;
            height: 100%;

            li {
                display: flex;
                align-items: center;
                height: 100%;
                padding-left: 14px;
                font-family: 'Proxima Nova';
                font-size: 14px;
                font-weight: 700;
                line-height: 17px;
                letter-spacing: 0.015em;
                cursor: pointer;

                &:hover {
                    background-color: rgba(254, 95, 30, 0.05);
                    color: $orange;
                    transition: 0.3s;
                }

                &:active {
                    background-color: rgba(254, 93, 30, 0.2);
                    color: $orange;
                }
            }

            .selected {
                background-color: rgba(254, 95, 30, 0.05);
                color: $orange;
            }
        }
    }

    .active {
        display: block;
    }
}

@media (max-width: 735px) {
    .sortByOther {
        width: 100%;
        justify-content: center;
        align-items: center;
        margin-left: 0;

        .sortSelector {
            top: 26px;
            right: calc(50% - 112px)
        }
    }
}