body{margin:0;font-family:-apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace}*{padding:0;margin:0;border:0}*,*:before,*:after{-moz-box-sizing:border-box;-webkit-box-sizing:border-box;box-sizing:border-box}:focus,:active{outline:none}a:focus,a:active{outline:none}nav,footer,header,aside{display:block}html,body{height:100%;width:100%;font-size:100%;line-height:1;font-size:14px;-ms-text-size-adjust:100%;-moz-text-size-adjust:100%;-webkit-text-size-adjust:100%}input,button,textarea{font-family:inherit}input::-ms-clear{display:none}button{cursor:pointer}button::-moz-focus-inner{padding:0;border:0}a,a:visited{text-decoration:none}a:hover{text-decoration:none}ul li{list-style:none}img{vertical-align:top}h1,h2,h3,h4,h5,h6{font-size:inherit;font-weight:400}@font-face{font-family:"Proxima Nova";src:url("./fonts/proximanova_regular.ttf");font-weight:normal;font-style:normal}@font-face{font-family:"Proxima Nova";src:url("./fonts/proximanova_bold.otf");font-weight:700;font-style:normal}@font-face{font-family:"Proxima Nova";src:url("./fonts/proximanova_extrabold.otf");font-weight:800;font-style:normal}body{background-color:#ffdf8c}#root{padding:45px;background-color:#ffdf8c;font-family:"Proxima Nova";color:#000}h1{font-family:"Proxima Nova";font-size:32px;font-weight:700;line-height:39px;letter-spacing:.01em}.app{max-width:1340px;margin:0 auto;border-radius:10px;background-color:#fff;box-shadow:0 0 20px 0 rgba(0,0,0,.1098039216)}.main{position:relative}@media(max-width: 600px){#root{padding:15px}}@media(max-width: 370px){h1{font-size:22px}}.header{display:flex;justify-content:space-between;align-items:center;height:130px;padding:50px 50px 40px 50px;border-bottom:1px solid #f6f6f6}.header .logoBlock{display:flex;justify-content:flex-start;align-items:center}.header .logoBlock .logoImg{margin-right:17px;cursor:pointer}.header .logoBlock .logoImg img{width:38px;height:auto}.header .logoBlock .logoText{display:flex;flex-direction:column;justify-content:space-between;align-items:flex-start}.header .logoBlock .logoText .name{font-family:"Proxima Nova";font-size:24px;font-weight:800;line-height:29px;letter-spacing:.01em;text-transform:uppercase}.header .logoBlock .logoText .slogan{font-size:16px;line-height:19px;color:#7b7b7b}.header .cartBlock .cartBtn{display:flex;justify-content:center;align-items:center;padding:15px 20px;border-radius:30px;background-color:#fe5f1e;font-family:"Proxima Nova";font-size:16px;font-weight:700;line-height:19px;color:#fff;cursor:pointer;-moz-user-select:none;-khtml-user-select:none;user-select:none}.header .cartBlock .cartBtn:hover{background-color:#ff7b47;transition:.3s}.header .cartBlock .cartBtn:active{background-color:#fd9871}.header .cartBlock .cartBtn .priceBlock{display:flex;justify-content:flex-end;align-items:center;min-width:34px;height:25px;border-right:1px solid rgba(255,255,255,.25)}.header .cartBlock .cartBtn .priceBlock .price::after{content:"₽";margin:0 13px 0 8px;font-family:"arial"}.header .cartBlock .cartBtn .cartCounter{display:flex;justify-content:flex-start;align-items:center;min-width:34px;margin-left:13px}.header .cartBlock .cartBtn .cartCounter img{position:relative;top:-2px;width:16px;height:auto;margin-right:8px}@media(max-width: 600px){.header{padding:50px 20px 40px 20px}}@media(max-width: 477px){.header{justify-content:center;flex-wrap:wrap;height:auto;padding:30px 20px 20px 20px}.header .logoBlock{margin-bottom:15px}.header .cartBlock{width:80%}.header .cartBlock .cartBtn{width:100%}}