@import './../../../index.scss';

.sortByCategory {
    overflow: auto;
    -ms-overflow-style: none;
    scrollbar-width: none;

    &::-webkit-scrollbar {
        display: none;
    }

    ul {
        display: flex;

        li {
            display: flex;
            justify-content: center;
            align-items: center;
            height: 46px;
            padding: 0 33px;
            margin-right: 9px;
            border-radius: 30px;
            background-color: $grayBG;
            font-family: 'Proxima Nova';
            font-style: normal;
            font-weight: 700;
            font-size: 16px;
            line-height: 19px;
            text-align: center;
            letter-spacing: 0.015em;
            color: $blackLight;
            cursor: pointer;
            -moz-user-select: none;
            -khtml-user-select: none;
            user-select: none;

            &:hover {
                background-color: #d8d8d8;
                transition: 0.3s;
            }

            &:active {
                background-color: $blackLight;
                color: #FFFFFF;
            }
        }

        .selected {
            background-color: $blackLight;
            color: #FFFFFF;

            &:hover {
                background-color: $blackLight;
                color: #FFFFFF;
            }
        }
    }
}

@media (max-width: 735px) {
    .sortByCategory {
        margin-bottom: 25px;
    }
}