@import './../../../index.scss';

.cartEmpty {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding-top: 50px;

    h1 {
        margin-bottom: 10px;
    }

    p {
        margin-bottom: 45px;
        font-family: 'Proxima Nova';
        font-style: normal;
        font-weight: 400;
        font-size: 18px;
        line-height: 145.4%;
        text-align: center;
        letter-spacing: 0.01em;
        color: #777777;
    }

    img {
        width: 300px;
        height: 255px;
        margin-bottom: 75px;
    }

    .backBtn {
        width: 210px;
        height: 46px;
        border-radius: 30px;
        background-color: $blackLight;
        font-family: 'Proxima Nova';
        font-style: normal;
        font-weight: 700;
        font-size: 16px;
        line-height: 19px;
        text-align: center;
        letter-spacing: 0.015em;
        color: #FFFFFF;

        &:hover {
            background-color: #404040;
            transition: 0.3s;
        }

        &:active {
            background-color: #575757;
            transition: 0.3s;
        }
    }
}

@media (max-width: 370px) {
    .cartEmpty {
        img {
            width: 100%;
            height: auto;
        }
    }
}